<template>
  <div >
     <div id="echart" class="charts-pie"></div>
     <h1>16783</h1>
  </div >
</template>

<script>
  import ECharts from 'echarts/lib/echarts'
  import 'echarts/lib/chart/pie'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/title'
  import 'echarts/lib/component/legend'
  
  export default {
   
    data() {
      return {
        charts: null
      }
    },
    props: {
      options: [Object],
      loading: {type:Boolean, default: true}
    },
    watch: {
      options: {
        handler (val) {
          this.charts.setOption(val)
        },
        deep: true
      }
    },
    mounted() {
      this.charts = ECharts.init(document.getElementById("echart"), {color: ['#c12e34', '#e6b600', '#0098d9','#2b821d', '#005eaa', '#339ca8']})
      let option = {
        title: {text: ''},
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        calculable: true,
        series: [
          {
            name:'',
            type:'pie',
            // roseType : 'area',
            label: {
              normal: {formatter: "{b} ({c})"}
            },
            data:[
              {value: 485, name:'fruit'},
              {value: 571, name:'vegetables'},
              {value: 690, name:'fish'},
              {value: 485, name:'meat'},
              {value: 5, name:'bee'},
              {value: 690, name:'pickles'},
            ]
          }
        ]
      }

      this.charts.setOption(option)
    },
    name: "charts-pie"
  }
</script>

<style scoped>
  .charts-pie {
    width: 100%;
    height: 460px;
  }
</style>



